#testimonials {
    margin-left: 0 !important; 
    margin-right: 0 !important; 
}

.testBox {
    width: 40rem;
    height:18rem;
    margin-bottom: 2rem;
}

.testCard {
    background: var(--color-bg-variant);
    text-align: center;
    padding: 2rem;
    border-radius: 2rem;
    user-select: none;
}

/* .testCard:hover {
    background-color: transparent;
    border: 1px solid var(--color-primary-variant)
} */

.testCard img {
    width: 4rem;
    border-radius: 50%;
    margin: 0 auto 1rem;
    border: 0.4rem solid var(--color-primary-variant);
}

.testCard h4 {
    height: 10rem
    
}

.testCard small {
    width: 80%;
    height: 6rem;
    margin: 0.8rem auto 0;
    color: var(--color-light);
    display: block;
    overflow: hidden;
}


/* ============= MEDIA QUERIES ( MEDUME DEVICES ) ============== */
@media screen and (max-width: 1024px) {
    .testBox {
        width: 36rem;
    }
}

/* ============= MEDIA QUERIES ( SMALL DEVICES ) ============== */
@media screen and (max-width: 600px) {
    .testBox {
        width: 20rem;
    }
    .testCard small {
        width: 95%;
    }
}