.contactContainer {
    display: flex;
    flex-wrap: wrap;
    margin: 0 10rem;
}

.contactOptions {
    display: flex;
    flex: 2;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 1rem;
}

.contactOptions article {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 0.8rem;
    width: 16rem;
    height: 10rem;
    background: var(--color-bg-variant);
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
    overflow: hidden;
}

.contactOptions article:hover {
    background-color: transparent;
    border: 1px solid var(--color-primary-variant)
}

.contactOptions svg {
    font-size: 1.6rem;
}

.contactOptions h5 {
    font-size: 0.7rem;
    color: var(--color-light);
}

.contactOptions span {
    text-align: center;
}

form {
    display: flex;
    flex: 2;
    flex-direction: column;
    gap: 1.2rem;
}

input, textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
    transition: var(--transition);
}

.btnPrimary {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.6rem
}

/* ==== spinner styles ==== */
.loader {
    width: 1rem;
    height: 1rem;
    border: 0.2rem solid #FFF;
    border-bottom-color: transparent;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
} 
  


/* ============= MEDIA QUERIES ( MEDIUM DEVICES ) ============== */
@media screen and (max-width: 1024px) {
    .contactContainer {
        margin: 0;
        gap: 1rem;
    }
}


/* ============= MEDIA QUERIES ( SMALL DEVICES ) ============== */
@media screen and (max-width: 800px) {
    .contactContainer {
        margin: 0;
        gap: 1rem;
        display: block;
    }
    .contactOptions {
        margin-bottom: 5rem;
    }
}