header {
    padding-top: 2rem;
}

.card {
    display: flex;
    align-items:center;
    justify-content:center;
    flex-direction: row;
    margin-top: 3.5rem;
}

.cardLinks {
    display: flex;
    flex:1;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.cardLinks svg {
    font-size: 1.4rem;
}

.cardImg {

}

.cardScroll {
    display: flex;
    flex:1;
    justify-content: center;
    align-items: center;
    transform: rotate(90deg);
}

.imgBox {
    width: 20rem;
    height: 22rem;
    background: linear-gradient(var(--color-primary), transparent);
    border-radius: 12rem 12rem 0 0;
    padding: 2rem;
    padding-top: 3rem;
    padding-bottom: 0;
}

.imgBox div {
    background-image: url('../../assets/me.png');
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
}

.cvLink {
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 1.2rem;
    padding-top: 2.5rem;
}

/* ============= MEDIA QUERIES ( MEDIUM DEVICES ) ============== */
@media screen and (max-width: 1024px) {
    
}


/* ============= MEDIA QUERIES ( SMALL DEVICES ) ============== */
@media screen and (max-width: 600px) {
    .card {
        flex-direction: column;
    }
    .cardLinks {
        flex-direction: row;
        margin-bottom: 1rem;
        gap: 2rem;
    }
    .cardScroll {
        transform: rotate(0);
        margin-top: 0.5rem;
    }
}