.portBox {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
}

.portCard {
    width: 20rem;
    max-height: 25rem;
    overflow: hidden;
    background: var(--color-bg-variant);
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
    margin: 1.4rem 1rem;
    padding: 1rem;
}

.portCard:hover {
    background-color: transparent;
    border: 1px solid var(--color-primary-variant)
}

.portCard img {
    width: 100%;
    height: 60%;
    object-fit: cover;
    border-radius: 2rem;
}

.portCard h4 {
    max-height: 9rem;
    overflow: hidden;
    padding: 0.6rem 0 0.8rem 0;
    font-size: 0.9rem;
    text-align: left;
}

.portCardBtnGroup {
    display: flex;
    gap: 0.4rem;
}


/* ============= MEDIA QUERIES ( Medium DEVICES ) ============== */
@media screen and (max-width: 1024px) {
    
}

/* ============= MEDIA QUERIES ( SMALL DEVICES ) ============== */
@media screen and (max-width: 600px) {
    .portCard {
        margin: 0.6rem 0; 
    }
}