.certContent {
    display: flex;
    width: 100wv;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    flex-wrap: wrap;
}

.certCard {
    width: 20rem;
    height: max-content;
    background: var(--color-bg-variant);
    border-radius: 0 0 2rem 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
    margin: 1rem;
}

.certCard:hover {
    background-color: transparent;
    border: 1px solid var(--color-primary-variant)
}

.certCardHeader {
    width: 100%;
    height: 5rem;
    border-radius: 0 0 2rem 2rem;
    background: var(--color-primary);
}

.certCardHeader h4 {
    color: var(--color-bg-variant);
}

.certCardBody {
    padding: 1.3rem 1rem 1rem;
}

.certCardBody img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 2rem;
}

.certCardBody article {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: row;
    gap: 1rem;
}

.certCardBody article h5, .certCardBody article svg {
    color: var(--color-light);
}

/* ============= MEDIA QUERIES ( SMALL DEVICES ) ============== */
@media screen and (max-width: 600px) {
    .certCardBody article svg {
        width: 20%;
    }
}