.exBox {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3rem;
    flex-wrap: wrap;
}

.exBoxCard {
    width: 30rem;
    height: max-content;
    background: var(--color-bg-variant);
    border-radius: 2rem;
    border: 1px solid transparent;
    padding: 2rem;
    padding-bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    transition: var(--transition);
}

.exBoxCard:hover {
    background-color: transparent;
    border: 1px solid var(--color-primary-variant)
}

.exBoxCardHeader {
    width: 100%;
    height: 15%;
}

.exBoxCardHeader h4 {
    color: var(--color-primary);
}

.exBoxCardBody {
    width: 100%;
    height: 85%;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 1.4rem 0 1rem 2.5rem;
}

.exBoxCardBody article {
    width: 40%;
    display: flex;
    height: auto;
    gap: 1.5rem;
    margin: 0 2rem 1.4rem 0;
}

.exBoxCardBody svg {
    margin-top:0.5rem
}


/* ============= MEDIA QUERIES ( MEDIUM DEVICES ) ============== */
@media screen and (max-width: 1024px) {
    .exBoxCard {
        height: auto;
    }
}


/* ============= MEDIA QUERIES ( SMALL DEVICES ) ============== */
@media screen and (max-width: 600px) {
    .exBox {
        display: flex;
        flex-direction: row;
    }
    .exBoxCard {
        width: '90%';
        height: auto;
        padding: 0;
        padding-top: 1rem;
    }
    .exBoxCardBody article {
        width: 100%;
    }
}