footer {
    width: 100%;
    min-height: 22rem;
    margin-top: 7rem;
    background: var(--color-primary);
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 2rem;
}

footer a {
    color: var(--color-bg);
    font-style: normal;
    margin-top: 1.5rem;
}  


.footerLinks {
    gap: 2rem;
}

.footerLinks a {
    color: var(--color-bg-variant);
    font-size: 0.9rem;
}

.footerLinks a:hover {
    color: var(--color-white);
    font-size: 0.9rem;
}

.footerSocials {
    display: flex;
    gap: 1.4rem;
}

.footerSocials a {
    background: var(--color-bg);
    padding: 0.6rem 0.6rem 0.3rem;
    border-radius: 30%;
    transition: var(--transition);
}

.footerSocials a:hover {
    background: var(--color-bg-variant);
    color: var(--color-bg);
}

.footerSocials svg {
    color: var(--color-white);
    font-size: 1.2rem;
}

.footerCopyRight {
    color: var(--color-bg);
    font-size: 0.7rem;
    font-weight: 100;
    margin-bottom: 4rem;
}


/* ============= MEDIA QUERIES ( SMALL DEVICES ) ============== */
@media screen and (max-width: 600px) {
    .footerLinks {
        flex-direction: column;
    }
    footer {
        padding-bottom: 2rem;
    }
}