
.aboutMe {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.imageBox {
    display: flex;
    width: 40%;
    align-items: center;
    justify-content: center;
}

.imageBoxBg {
    width: 20rem;
    height: 20rem;
    border-radius: 1rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    /* background: red; */
}

.img {
    background-image: url("../../assets/me-about.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    width: 20rem;
    height: 20rem;
    border-radius: 1rem;
    transform: rotate(10deg);
    transition: var(--transition);
}

.img:hover {
    transform: rotate(0);
}

.detail {
    display: flex;
    width: 60%;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    padding-right: 5rem;
}

.detailBoxs {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    margin-bottom: 2rem;
}

.detailBoxItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 10rem;
    height:10rem;
    padding: 10px;
    padding-top: 5px;
    background-color: var(--color-bg-variant);  
    border: 1px solid transparent;
    border-radius: 10px;
    transition: var(--transition);
}

.detailBoxItem:hover {
    background-color: transparent;
    border: 1px solid var(--color-primary-variant)
}

.detailBoxItem span {
    text-align: center;
    margin-top: 0.2rem;
}


.txtDesc {
    margin-bottom: 2rem;
    color: var(--color-light);
    text-align: justify;
}


/* ============= MEDIA QUERIES ( MEDIUM DEVICES ) ============== */
@media screen and (max-width: 1024px) {
    .aboutMe {
        flex-direction: column;
    }
    .imageBox {
        margin: auto;
        margin-bottom: 5rem;
    }
    .detail {
        margin: auto;   
        align-items: center;
        padding: 0;
    }
}


/* ============= MEDIA QUERIES ( SMALL DEVICES ) ============== */
@media screen and (max-width: 600px) {
    .detailBoxs {
        flex-direction: column;
    }
    .detail {
        width: 100%;
    }
}